:root{--primary-color: #3f4257;--secondary-color: #ff5e3a;--primary-text-color: #9a9fbf;--active-text-color: #515365;--danger: #f92552;--success: #08ddc1}body{background-color:#edf2f6}*{margin:0;padding:0;outline:none;font-family:Roboto, -apple-system, system-ui, BlinkMacSystemFont, "Segoe UI", "Helvetica Neue", Arial, sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;box-sizing:border-box}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}a{text-decoration:none;color:var(--primary-text-color)}ul{list-style:none}.sidebar{position:fixed;right:0;top:80px;background-color:#fff;width:270px;border-radius:8px;max-height:60%;overflow-y:scroll;transform:translateX(75%);transition:.5s ease}.sidebar ::-webkit-scrollbar-track{box-shadow:inset 0 0 6px rgba(0,0,0,0.3);border-radius:10px;background-color:#F5F5F5}.sidebar::-webkit-scrollbar{width:12px;background-color:#FFF}.sidebar::-webkit-scrollbar-thumb{border-radius:10px;box-shadow:inset 0 0 6px rgba(0,0,0,0.3);background-color:#ccc}.sidebar:hover{transform:translate(0)}.sidebar__friends{width:100%;padding:10px 0}
